            <el-col :span='12'>
              <el-form-item label="公司鉴定结果" prop="company_status" :rules="{required: true,
    message: '请选择公司鉴定结果', trigger: 'change' }">
                <el-select clearable v-model="params.company_status" disabled>
                  <el-option label="待厂检" :value="1">待厂检</el-option>
                  <el-option label="理赔" :value="2">理赔</el-option>
                  <el-option label="不理赔" :value="3">不理赔</el-option>
                </el-select>
              </el-form-item>
