          <el-row>
            <el-col :span='12'>
              <el-form-item label="剩余花深" prop="surplus_flowerdeep" :rules="{required: true, validator: (rule, value, callback) => checkPositive(rule, value, callback,'剩余花深'), trigger: 'change' }">
                <el-input type="text" v-model="params.surplus_flowerdeep" @input="computeDeep('surplus_flowerdeep')" @change="changeComputeDeep(params, 'surplus_flowerdeep')" />
              </el-form-item>
            </el-col>
            <el-col :span='12'>
              <el-form-item label="厂免花深" prop="factory_flowerdeep" :rules="{ validator: (rule, value, callback) => checkPositive(rule, value, callback,'厂免花深'),
     trigger: 'change' }">
                <el-input type="text" v-model="params.factory_flowerdeep" @input="computeDeep('factory_flowerdeep')" @change="changeComputeDeep(params, 'factory_flowerdeep')" />
              </el-form-item>
            </el-col>

          </el-row>
          <el-row>
            <el-col :span='12'>
              <el-form-item label="公司照顾花深" prop="care_flowerdeep" :rules="{ validator: (rule, value, callback) => checkPositive(rule, value, callback,'公司照顾花深'),
     trigger: 'change' }">
                <el-input type="text" v-model="params.care_flowerdeep" @input="computeDeep('care_flowerdeep')" @change="changeComputeDeep(params, 'care_flowerdeep')" />
              </el-form-item>
            </el-col>
            <el-col :span='12'>
              <el-form-item label="收费花深" prop="charge_flowerdeep">
                <span>{{ params.charge_flowerdeep }}</span>
              </el-form-item>
            </el-col>

          </el-row>
          <el-row>
            <el-col :span='12'>
              <el-form-item label="毫米磨损费" prop="mm_wearprice" :rules="{required: true, validator: (rule, value, callback) => checkPositive(rule, value, callback,'毫米磨损'),
     trigger: 'change' }">
                <el-input type="text" v-model="params.mm_wearprice" @input="computeDeep('mm_wearprice')" @change="changeComputeDeep(params, 'care_flowerdeep')" />
              </el-form-item>
            </el-col>
            <el-col :span='12'>
              <el-form-item label="磨损费" prop="wear_price" :rules="{ validator: (rule, value, callback) => checkPositive(rule, value, callback,'磨损费'),
     trigger: 'blur' }">
                <el-input type="text" v-model="params.wear_price" @input='computedClaimPrice("wear_price")' @change="changeComputedClaimPrice('wear_price')" />
              </el-form-item>
            </el-col>

          </el-row>
          <el-row>
            <el-col :span='12'>
              <el-form-item label="内胎售价" prop="intube_price" :rules="{ validator: (rule, value, callback) => checkPositive(rule, value, callback,'内胎售价'),
     trigger: 'change' }">
                <el-input type="text" v-model="params.intube_price" @input="computePrice('intube_price')" @change="changeComputePrice(params, 'intube_price')" />
              </el-form-item>
            </el-col>
            <el-col :span='12'>
              <el-form-item label="垫带售价" prop="flap_price" :rules="{ validator: (rule, value, callback) => checkPositive(rule, value, callback,'垫带售价'),
     trigger: 'change' }">
                <el-input type="text" v-model="params.flap_price" @input="computePrice('flap_price')" @change="changeComputePrice(params, 'flap_price')" />
              </el-form-item>
            </el-col>

          </el-row>
          <el-row>
            <el-col :span='12'>
              <el-form-item label="内垫费" prop="inflap_price" :rules="{ validator: (rule, value, callback) => checkPositive(rule, value, callback,'内垫费'),
     trigger: 'change' }">
                <el-input type="text" v-model="params.inflap_price" @input='computedClaimPrice("inflap_price")' @change="changeComputedClaimPrice('inflap_price')" />
              </el-form-item>
            </el-col>
            <el-col :span='12'>
              <el-form-item label="最新销售价" prop="new_goods_price" :rules="{ validator: (rule, value, callback) => checkPositive(rule, value, callback,'最新销售价'),
     trigger: 'change' }">
                <el-input type="text" v-model="params.new_goods_price" @input='computedClaimPrice("new_goods_price")' @change="changeComputedClaimPrice('new_goods_price')" />
              </el-form-item>
            </el-col>
          </el-row>
          <el-row>
            <el-col :span='12'>
              <el-form-item label="理赔单价" prop="claim_price" :rules="{ validator: (rule, value, callback) => checkPositive(rule, value, callback,'理赔单价'),
     trigger: 'change' }">
                <el-input type="text" v-model="params.claim_price"  />
              </el-form-item>
            </el-col>
            <el-col :span='24'>
