        <el-button type="primary" @click="submit" size="mini" v-if='type !== 3'>保存</el-button>
        <template v-if="params.auditor_status == 1 && type === 3">
          <el-button type="primary" @click="audit('auditorPassCompanyAppraisal')" v-if="authority.b_pass==1">审核</el-button>
          <el-button @click="audit('auditorInvalidCompanyAppraisal')" v-if="authority.b_invalid==1">作废</el-button>
